html {
  box-sizing: border-box;
  font: 10px/1.6 sans-serif; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-size: 1.4rem; }
  @media (min-width: 960px) {
    body {
      font-size: 1.6rem; } }

img {
  max-width: 100%;
  height: auto; }

table {
  border-collapse: collapse; }

th, td {
  border: 1px solid;
  border-color: inherit;
  padding: .5em 1em; }

h1, h2, h3, h4, h5, h6 {
  margin: 1rem 0; }

p, ol, ul, dl, table, img, code, pre, blockquote, hr {
  margin: 1rem 0; }

ul {
  padding-left: 1.125em; }

hr {
  border: 0;
  border-top: 1px solid currentColor;
  opacity: .2; }

svg {
  fill: currentColor; }

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.sticky {
  top: 1rem;
  position: sticky; }

.icon {
  fill: currentColor; }

svg.svg {
  width: 1em;
  height: 1em;
  fill: currentColor;
  vertical-align: -.15em; }
  svg.svg--wider {
    width: 2em; }

.compactmargins > * {
  margin-top: 0;
  margin-bottom: 0; }

.childmargins > *:first-child {
  margin-top: 0; }

.childmargins > *:last-child {
  margin-bottom: 0; }

.inlinelist {
  list-style-type: none;
  margin: 0;
  padding: 0; }
  .inlinelist li {
    display: inline;
    margin: 1em 0;
    padding: 0; }

